import Header from "@/components/common/Header";
import XDialog from "@/components/common/XDialog";

export default {
  name: "",
  props: [""],
  data() {
    return {
      showDialogFlag: false,
      TitleObjData: {
        titleContent: "通证",
        showLeftBack: true,
        showRightMore: false
      },
      userInfo: null,
      descInfo:["通证是用户在卡戳网的唯一识别号","用户完成实名认证之后可以获取通证"]
    };
  },

  components: {
    Header,
    XDialog
  },

  computed: {
    conSty() {
      return { height: document.documentElement.clientHeight - 45 + "px" };
    }
  },
  created() {
    this.getUserInfo();
  },
  beforeMount() {},

  mounted() {},

  methods: {
    getUserInfo() {
      this.userInfo = this.GLOBAL.getSession("userLoginInfo");
    },
    watchAllCode() {
      this.$refs.dialogCon.showToastFn(this.userInfo.rule);
    },
    getQRcode() {
      this.$router.push("/tokenqrcode");
    }
  },

  watch: {}
};



